














































import Vue from 'vue'
import Monster from '../model/monsters/Monster'

export default Vue.extend({
  props: {
    monster: {
      type: (Monster as unknown) as () => Monster,
      required: true
    }
  },
  computed: {
    headers(): string[] {
      return ['M', 'F', 'S', 'A', 'W', 'H']
    },
    values(): number[] {
      return [
        this.monster.move,
        this.monster.fight,
        this.monster.shoot,
        this.monster.armour,
        this.monster.will,
        this.monster.health
      ]
    },
    attributes(): string[] {
      return this.monster.attributes.map(Monster.attributeAsString)
    }
  }
})
