
























import Vue from 'vue'
import { allMonsters } from '../utils'
import Monster from '@/model/monsters/Monster'
import MonsterDialog from '@/dialogs/Monster.vue'

export default Vue.extend({
  components: {
    MonsterDialog
  },
  computed: {
    monsters() {
      return allMonsters()
    }
  },
  data() {
    return {
      activeMonster: null as Monster | null
    }
  }
})
