
























import Vue from 'vue'
import { PropValidator } from 'vue/types/options'

export default Vue.extend({
  props: {
    open: {
      type: Boolean,
      required: true
    },
    header: {
      type: String,
      required: false,
      default: 'Please Confirm'
    },
    message: {
      type: String,
      required: false,
      default: "You can't undo this action, are you certain that you want to continue?"
    }
  }
})
